.flipCard {
    transform: rotateY(180deg);
}

.card {
    position: relative;
    width: 161.25px;
    height: 215px;
}

.front,
.back {
    width: 100%;
    height: 100%;
    pointer-events: none;
    backface-visibility: hidden;
    transition-duration: 1000ms;
    border-radius: 5px;
    box-sizing: border-box;
    position: absolute;
}

.back {
    border: 3px solid black;
}